define('taoQtiPrint/qtiPrintRenderer/renderers/interactions/NotSupported',[
    'lodash',
    'i18n',
    'tpl!taoQtiPrint/qtiPrintRenderer/tpl/interactions/notSupported',
    'taoQtiPrint/qtiPrintRenderer/helpers/container'
], function (_, __, tpl, getContainer) {
    'use strict';

    /**
     * Creates a fallback interaction to warn about lack of support for a particular QTI class
     * @param qtiClass
     * @returns {Object}
     */
    function interactionNotSupported(qtiClass) {
        return {
            qtiClass: qtiClass,
            template: tpl,
            getContainer: getContainer,
            resetResponse: _.noop,
            setResponse: _.noop,
            getData: function getData(interaction, data) {
                return _.merge(data || {}, {
                    message: __('The interaction "%s" is not supported for printing purpose!', qtiClass)
                });
            }
        };
    }

    return interactionNotSupported;
});
